.intro-marquee {
  display: flex;
  width: max-content;
}

.intro-marquee.marquee-left {
  animation: scroll-left 60s linear infinite;
}

.intro-marquee.marquee-right {
  animation: scroll-right 60s linear infinite;
}

/**
 * Cloned marquee rows should scroll at double speed.
 * Clones from JS get [data-clone="true"] attribute.
 * So: select .intro-marquee[data-clone="true"]
 */
.intro-marquee[data-clone='true'].marquee-left {
  animation: scroll-left 60s linear infinite;
}

.intro-marquee[data-clone='true'].marquee-right {
  animation: scroll-right 60s linear infinite;
}

/* Keyframes for left scroll */
@keyframes scroll-left {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

/* Keyframes for right scroll */
@keyframes scroll-right {
  0% {
    transform: translateX(-50%);
  }
  100% {
    transform: translateX(0);
  }
}
